import React from 'react';


function Card( {name, email, username} ) {
    return (
      <div className="bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5">
          <img src={`https://robohash.org/${username}?200x200`} alt={username} />
          <div>
            <h2>{name}</h2>
            <p>{email}</p>
          </div>
      </div> 
    );
  }
  
  export default Card;
  